.shopping-cart .cart-icon {
  margin-right: 10px;
}
.shopping-cart .cart-icon span:hover {
  color: #e84545;
}
.shopping-cart .cart-info p:hover {
  color: #80bb01;
}
.shopping-cart .cart-icon span {
  font-size: 40px;
}

.shopping-cart .cart-info a {
  -webkit-transition: 0s;
  -o-transition: 0s;
  transition: 0s;

}

.shopping-cart .cart-info p {
  -webkit-transition: 0s;
  -o-transition: 0s;
  transition: 0s;

}

.shopping-cart .cart-info span {
  display: block;
}

.shopping-cart .cart-info span span {
  display: inline-block;
}

.cart-floating-box {
  position: absolute;
  z-index: 999999;
  right: 0;
  width: 360px;
  background-color: #ffffff;
  padding: 20px;
}
.floating-cart-btn a {
  background: #fff;
  border: 2px solid #e0e0e0;
  color: #323232;
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  height: 47px;
  line-height: 43px;
  margin: 0 4px;
  min-width: 130px;
  padding: 0 15px;
  text-align: center;
  text-transform: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}
.floating-cart-btn a:hover {
  background: #2b2e4a;
  border-color: #80bb01;
  color: #fff;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .cart-floating-box {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .cart-floating-box {
    display: none;
  }
}

@media only screen and (max-width: 575px) {
  .cart-floating-box {
    display: none;
  }
}

@media only screen and (max-width: 479px) {
  .cart-floating-box {
    display: none;
  }
}

#cart-floating-box .cart-items {
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
}

#cart-floating-box .cart-float-single-item {
  position: relative;
  margin-bottom: 20px;
}

#cart-floating-box .cart-float-single-item span.remove-item {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 10px;
}

#cart-floating-box .cart-float-single-item .cart-float-single-item-image {
  width: 25%;
  margin-right: 10px;
}

#cart-floating-box .cart-float-single-item .cart-float-single-item-image img {
  display: inline-block;
  border: 1px solid #ddd;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.cart-float-single-item .cart-float-single-item-image img:hover {
  border: 1px solid #80bb01;
}

.cart-float-single-item .cart-float-single-item-desc p {
  margin-bottom: 5px;
}
.cart-float-single-item .cart-float-single-item-desc p.product-title {
  font-weight: 500;
  color: #333;
}
.cart-float-single-item .cart-float-single-item-desc p.product-title:hover {
  color: #80bb01;
}

.cart-floating-box .cart-calculation .calculation-details {
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
}

.cart-floating-box .cart-calculation .calculation-details p {
  font-weight: 500;
  color: #333;
  font-size: 18px;
  line-height: 20px;
  margin-bottom: 20px;
}

.cart-floating-box .cart-calculation .calculation-details p span {
  float: right;
}

.cart-floating-box .cart-calculation .calculation-details p.shipping {
  border-bottom: 1px solid #333;
  margin-bottom: 10px;
  padding-bottom: 10px;
}

.cart-floating-box .cart-calculation .checkout-button a {
  background-color: #333;
  color: #fff;
  padding: 10px 15px;
  display: inline-block;
  margin-top: 25px;
  margin-left: 25px;
  font-weight: 500;
  border-radius: 3px;
}

.cart-floating-box .cart-calculation .checkout-button a:hover {
  background-color: #80bb01;
}
