@media only screen and (min-width: 768px) and (max-width: 991px) {
  .login-form {
    padding: 15px;
  }
}
.login-form label {
  display: block;
  font-size: 14px;
  margin-bottom: 12px;
  font-weight: 500;
  text-transform: capitalize;
}
.login-form .nice-select {
  width: 100%;
  background-color: transparent;
  border: 1px solid #999999;
  border-radius: 50px;
  line-height: 23px;
  padding: 10px 20px;
  font-size: 14px;
  height: 45px;
  color: #666666;
  margin-bottom: 15px;
}
.login-form .nice-select::after {
  width: 6px;
  height: 6px;
  border-width: 1px;
  right: 20px;
  border-color: #666666;
}
.login-form .nice-select .current {
  color: #666666;
  display: block;
  line-height: 23px;
}
.login-form .nice-select .list {
  width: 100%;
}
.login-form input {
  width: 100%;
  background-color: transparent;
  border: 1px solid #999999;
  border-radius: 50px;
  line-height: 23px;
  padding: 10px 20px;
  font-size: 14px;
  color: #666666;
  margin-bottom: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.login-form input[type="checkbox"] {
  width: auto;
}
.login-form .check-box {
  float: left;
  margin-right: 70px;
}
.login-form .check-box:last-child {
  margin-right: 0;
}
.login-form .check-box input[type="checkbox"] {
  display: none;
}
.login-form .check-box input[type="checkbox"] + label {
  position: relative;
  padding-left: 30px;
  line-height: 20px;
  font-size: 14px;
  font-weight: 400;
  color: #222222;
  margin: 0;
}
.login-form .check-box input[type="checkbox"] + label::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  display: block;
  border: 2px solid #999999;
  content: "";
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.login-form .check-box input[type="checkbox"] + label::after {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  content: "\f00c";
  font-family: Fontawesome;
  font-size: 12px;
  line-height: 20px;
  opacity: 0;
  color: #222222;
  width: 20px;
  text-align: center;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.login-form .check-box input[type="checkbox"]:checked + label::before {
  border: 2px solid #222222;
}
.login-form .check-box input[type="checkbox"]:checked + label::after {
  opacity: 1;
}
