#aa-slider img {
  width: 100%;
  max-height: 100vh;
}
div#aa-slider {
  margin-bottom: 100px;
}
#aa-slider .swiper-button-next {
  width: 70px;
  height: 70px;
  line-height: 70px;
  background: rgba(0, 0, 0, 0.5);
  padding: 5px;
  color: white;
  border-radius: 50%;
  right: 5%;
}

#aa-slider .swiper-button-prev {
  width: 70px;
  height: 70px;
  line-height: 70px;
  background: rgba(0, 0, 0, 0.5);
  padding: 5px;
  color: white;
  border-radius: 50%;
  left: 5%;
}
#aa-slider .swiper-button-prev:hover,
#aa-slider .swiper-button-next:hover {
  background: #111;
  transition: 1s;
}
.bg-1 {
  background-image: url("http://placehold.it/1920x900");
}
#aa-slider {
  float: left;

  width: 100%;
}
#aa-slider .aa-slider-area {
  display: inline;
  float: left;
  width: 100%;
}
#aa-slider .aa-slider-area .seq-title {
  left: 19%;
  margin-right: 0;
  position: absolute;
  top: 30%;
  width: 57%;
  text-align: center;
}
#aa-slider .aa-slider-area .seq-title span {
  background-color: #fff;
  display: inline-block;
  padding: 6px 15px;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 10px;
}
#aa-slider .aa-slider-area .seq-title h2 {
  background-color: rgba(255, 255, 255, 0.6);
  color: #333;
  display: inline-block;
  float: none;
  font-size: 50px;
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  margin-top: 20px;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}
#aa-slider .aa-slider-area .seq-title p {
  color: #fff;
  display: block;
  text-align: center;
  letter-spacing: 0.5px;
}
#aa-slider .aa-slider-area .seq-title .aa-shop-now-btn {
  margin-top: 25px;
}
#aa-slider .aa-slider-area .seq .seq-next {
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
#aa-slider .aa-slider-area .seq .seq-next:hover,
#aa-slider .aa-slider-area .seq .seq-next:focus {
  border: 1px solid #f9f9f9;
  color: #fff;
}
#aa-slider .aa-slider-area .seq .seq-prev {
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
#aa-slider .aa-slider-area .seq .seq-prev:hover,
#aa-slider .aa-slider-area .seq .seq-prev:focus {
  border: 1px solid #f9f9f9;
  color: #fff;
}
