/*Extra small devices (portrait phones, less than 576px)*/
@media (min-width: 320px) and (max-width: 575px) {
  #aa-slider .aa-slider-area .swiper-button-prev,
  #aa-slider .aa-slider-area .swiper-button-next {
    left: 120%;
  }

  #header .aa-logo a {
    font-family: "Raleway", sans-serif;
    font-size: 10px;
  }
  #header .aa-logo a:hover {
    color: red;
  }
  #header .aa-logo a > span {
    font-size: 12px;
  }

  #header .aa-logo a p span {
    font-size: 7px;
  }
  #header .aa-logo a p:hover {
    color: black;
  }
  #aa-slider {
    float: left;
    display: inline;
    width: 100%;
  }

  #aa-slider .aa-slider-area .seq-screen .seq-title {
    left: 19%;
    margin-right: 0;
    position: absolute;
    top: 10%;
    width: 57%;
    text-align: center;
  }
  #aa-slider .aa-slider-area .seq-screen .seq-title span {
    font-size: 10px;
  }
  #aa-slider .aa-slider-area .seq-screen .seq-title h2 {
    font-size: 12px;
  }

  #aa-slider .aa-slider-area .seq-screen .seq-title p {
    font-size: 8px;
  }
  .all-title-box::before {
    background: rgba(0, 0, 0, 0.6);
    content: "";
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
  }
  a.myBtn {
    font-size: 12px;
    color: red;
    padding: 1px;
  }
  #navbar-2 .header-advance-search {
    display: none;
  }
  .mainHolder nav {
    display: none;
  }
  #cart {
    display: none;
  }
  #cart-floating-box{
    display: none;
  }
}

